@import url('https://fonts.googleapis.com/css2?family=Kaushan+Script&family=Merriweather:wght@300&display=swap');


body {
  background-image: linear-gradient(to right bottom,  #00F260,#0575E6);
  background-attachment: fixed;
  
}
a{
  text-decoration: none;
  color: #354548;
}
a:hover {
  text-decoration: none;
  color: #000;
}
.app__container {
  padding-top: 50px;
}
.app__row {
  height: 86vh;
}

.app__main-content {
  background-color: #fff;
  border-radius: 20px;
  padding: 25px 15px;
  box-shadow: 0 0 10px black;
}

/* sidebar */

.sidebar {
  background-color: #fff;
  height:100%;
  border-radius: 20px;
  padding: 25px 15px;
  box-shadow: 0 0 10px black;
  text-align: center;
}

.sidebar__avatar {
  height: 110px;
  width: 120px;
  object-fit: contain;
  border-radius: 30px;
}

.sidebar__icon {
  width: 25px;
  height: 25px;

}
.sidebar__name {
  font-family: Kaushan Script;
  margin: 20px 0;
  font-size: 1.7rem;
}
.sidebar__name>span {
  color: #00F260;
}
.sidebar__item {
  margin-bottom: 6px;
  padding-top: 6px;
  height: 40px;
  font-size: 1.05rem;
  border-radius: 30px;
}
.sidebar__title {
  background-color: #f1f1f1;
  border-radius: 30px;
}
.sidebar__contact {
  background-color: #dbdddf;
  padding: 2px 0;
  margin: 20px -15px;

}
.sidebar__email {
  background-image: linear-gradient(to right bottom, #00F260,#0575E6);
  border-radius: 30px;
  color: #fff;
  margin: 15px 25px 0px;
  cursor: pointer;
  transition: all 3s linear;
}
.sidebar__email:hover{
  transform: scale(1.2);
}
.sidebar__github:hover,
.sidebar__resume:hover{
  background-color: #f1f1f2;
  color: #000;
}
@media(max-width:991px){
  .app__main-content {
    margin: 30px 0;
  }
}

/* navbar */
.navbar__items {
  display: flex;
}
.navbar__active {
  font-size: 2rem;
  font-weight: 700;
  color: #354358;
  border-bottom: 4px solid #00F260;
}
.navbar__item {
  margin-right: 20px;
  text-decoration: none;
  color: #354358;
  font-size: pointer;
}
.navbar__item:hover{
  background: -webkit-linear-gradient( #00F260,#0575E6);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
/* about */
.about {
  padding:20px;
  margin: -35px;
}
.about__intro {
  font-size: 1.1rem;
  padding: 20px;
  margin: 10px 0;
}
.about__container {
  background-color: #dbdddf;
  padding: 20px;
}
.about__heading {
  font-size: 25px;
  font-weight: 700;
  color: #354358;
  margin-left: 10px;
}
/*skill-card*/
.skill-card {
  background-color: #fff;
  margin: 10px;
  padding: 10px;
  display: flex;
  flex-direction: row;
  border-radius: 10px;
}
.skill-card__icon {
  width: 40px;
  height: 40px;
}
.skill-card-body {
  margin-left: 15px;
}
.skill_card__content {
  margin: 0;
  font-size: 0.9rem;
  color: #6e7e91;
}